#pair{
  border: 2px solid #f8efcf;
  border-radius: 3px;
  padding: 10px 20px;
  width: 50vw;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;

  #end{

    &>*{
      position: relative;

      &:hover{
        .phantom{
          animation: pulse-btn 1s infinite;
        }
      }

      @keyframes pulse-btn {
        0% {
          opacity: 1;
          font-size: 100%;
        }
        100% {
          opacity: 0;
          font-size: 150%;
        }
      }
    }

    .phantom{
      position: absolute;
      z-index: -1;
      color: inherit;
    }
  }
}
